import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import Sc_info from '@/components/sc_info'
import Contact from '@/components/contact'
import Special_major_accouting from '@/components/special_major_accouting'
import Special_major_accouting1 from '@/components/special_major_accouting1'
import Special_major_artdesign from '@/components/special_major_artdesign'
import Special_major_artdesign1 from '@/components/special_major_artdesign1'
import Special_major_computer from '@/components/special_major_computer'
import Special_major_computer1 from '@/components/special_major_computer1'
import Register_guide from '@/components/register_guide'
import Admission_policy from '@/components/admission_policy'
import School_admission_brochure from '@/components/school_admission_brochure'
import Enrollment_plan from '@/components/enrollment_plan'
import Sign_online from '@/components/sign_online'
import News1 from '@/components/news_page1'
import News2 from '@/components/news_page2'
import News3 from '@/components/news_page3'
import News4 from '@/components/news_page4'

Vue.use(Router)

export default new Router({
  	routes: [
	    {
	      	path: '/',
	      	name: 'index',
	      	component: Index
	    },
	    {
	      	path: '/sc_info',
	      	component: Sc_info
	    },
	    {
	      	path: '/contact',
	      	component: Contact
	    },
	    {
	      	path: '/special_major_accouting',
	      	component: Special_major_accouting
	    },
	    {
	      	path: '/special_major_accouting1',
	      	component: Special_major_accouting1
	    },
	    {
	      	path: '/special_major_artdesign',
	      	component: Special_major_artdesign
	    },
	    {
	      	path: '/special_major_artdesign1',
	      	component: Special_major_artdesign1
	    },
	    {
	      	path: '/special_major_computer',
	      	component: Special_major_computer
	    },
	    {
	      	path: '/special_major_computer1',
	      	component: Special_major_computer1
	    },
	    {
	      	path: '/register_guide',
	      	component: Register_guide
	    },
	    {
	      	path: '/admission_policy',
	      	component: Admission_policy
	    },
	    {
	      	path: '/school_admission_brochure',
	      	component: School_admission_brochure
	    },
	    {
	      	path: '/enrollment_plan',
	      	component: Enrollment_plan
	    },
	    {
	      	path: '/sign_online',
	      	component: Sign_online
	    },
	    {
	      path: '/news_page1/:id',
	      name: 'news',
	      component: News1
	    },
	    {
	      path: '/news_page2/:id',
	      name: 'news2',
	      component: News2
	    },
	    {
	      path: '/news_page3/:id',
	      name: 'news3',
	      component: News3
	    },
	    {
	      path: '/news_page4/:id',
	      name: 'news4',
	      component: News4
	    },
  	]
})



