<script>
export default {
	data(){
		return{
			dynList:[],
			noticeList:[],
			empList:[],
			stuList:[],
			teacherList:[],
			lunList:[],
			hList:[],
			identity:''
		}
	},
	created(){
		var _self = this;
		var url="http://api.7171.la/api/article";            
		  this.$http.post(url,{token:'ahlhsw',code:'6c9d1f7505ecf9e34e5ffd69f8b3c5ff',type:'38,39'}
			).then(function (data) {
       		var json = data.data;
		  	_self.totalList = json;
			for (var i = 0;i<_self.totalList.length;i++) {
				if(_self.totalList[i].type == "38"){
					if(_self.dynList.length<4){
						_self.dynList.push(_self.totalList[i]);
					}
				}
				if(_self.totalList[i].type == "39"){
					if(_self.noticeList.length<4){
						_self.noticeList.push(_self.totalList[i]);
					}
				}
			}
    	});
	},
	methods: {
	    select(uri){
	    	this.$router.push({ path: uri });
	    },
	    validate(str){
	       	var reg= /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/; 
			if(reg.test(str) === false) { 
				document.getElementById('identity').style.display = 'block';
				document.getElementById('btn').setAttribute('disabled','disabled');
	       	}else{
	       		document.getElementById('identity').style.display = 'none';
	       		$("#btn").removeAttr("disabled");
	       	}
    	},
    	submitForm(event) {
            event.preventDefault();
            validate(this.identity);
            
            let config = {
              headers: {
                'Content-Type': 'multipart/form-data'
              }
            }
 
      /*      this.$http.post('/upload', formData, config).then(function (res) {
              if (res.status === 2000) {
	//	                这里做处理
              }
            })*/
        }
	},
	activated(){
		document.documentElement.scrollTop = 0
	},
	mounted(){
	}
	
}
